/* eslint-disable no-undef */
/* eslint-disable no-redeclare */
/* eslint-disable no-unused-vars */
const PATTERN = {
  h: {
    h: {
      cIndex: 0,
      pIndex: 0,
    },
    v: {
      cIndex: 1,
      pIndex: 1,
    },
  },
  v: {
    h: {
      cIndex: 0,
      pIndex: 1,
    },
    v: {
      cIndex: 1,
      pIndex: 0,
    },
  },
};

function calculateProduct(construction, product, method) {
  /**
   * Output: {amount: x (product), extraSize: x cm, modSize: x cm, needMod: boolean}
   */
  function rawCalculateByDirection(direction) {
    const index = PATTERN[method][direction];
    const cLenght = construction[index["cIndex"]];
    const pLenght = product[index["pIndex"]];
    const amount = Math.floor(cLenght / pLenght);
    const modSize = cLenght % pLenght;
    var needMod = false;

    if (modSize > 30 || modSize == 0) {
      needMod = true;
      //console.log("No extra product!");
    } else {
      //console.log("Need extra product!");
      var extraSize = Math.ceil(modSize / 2);
      if (extraSize < 5) extraSize = 5;
    }

    return {
      amount,
      modSize,
      extraSize,
      needMod,
      length: pLenght,
    };
  }

  function splitOneProductByDimension(modLength, productLength) {
    let amount = Math.floor(productLength / modLength);
    let modSize = productLength % modLength;
    return {
      amount,
      modSize,
    };
  }

  function calculate() {
    var totalProduct = 0;
    var position = [];
    var redundant = [];
    var extraProduct = [];
    var column = rawCalculateByDirection("h");
    var row = rawCalculateByDirection("v");
    var cWidth = construction[0];
    var cHeight = construction[1];
    var pWidth = method == "h" ? product[0] : product[1];
    var pHeight = method == "h" ? product[1] : product[0];

    //console.log("Col: ", column);
    //console.log("Row: ", row);

    // Construction square less than a product square.
    if (column.amount == 0 && row.amount == 0) {
      totalProduct = 1;
      position.push([
        {
          type: "product",
          size: [column.modSize, row.modSize],
        },
      ]);
    } else {
      // 1 column
      if (column.amount == 0) {
        let amount = row.amount;
        let splitProductInRow = splitOneProductByDimension(cWidth, pWidth);
        //console.log("splitProductInColumn", splitProductInRow);
        let splitAmount = splitProductInRow.amount;
        let productAmount = Math.ceil(amount / splitAmount);
        //console.log("Need:", productAmount);

        totalProduct = +productAmount;
        for (let i = 1; i <= amount; i++) {
          position.push([
            {
              type: "product",
              size: [cWidth, pHeight],
            },
          ]);
        }

        let redundantAmount = splitAmount * productAmount - amount;

        if (row.needMod && row.modSize !== 0) {
          if (redundantAmount > 0) {
            position.push([
              {
                type: "product",
                size: [cWidth, row.modSize],
              },
            ]);
          } else {
            totalProduct += 1;
            let modWidth = cWidth;
            let modHeight = row.modSize;
            position.push([
              {
                type: "product",
                size: [modWidth, modHeight],
              },
            ]);
          }
        } else if (row.extraSize) {
          // Extra product
          // if (redundantAmount > 0) {
          //     redundantAmount --;
          //     position.push([{
          //       type: "product",
          //       size: [cWidth, row.extraSize],
          //     }]);
          //     position.unshift([{
          //       type: "product",
          //       size: [cWidth, row.extraSize],
          //     }]);
          // } else {
          let val = Math.floor(product[0] / cWidth);
          let totalExtra = 0;
          if (val >= 2) {
            totalExtra += 1;
          } else {
            totalExtra += 2;
          }

          extraProduct.push({
            size: [product[0], row.extraSize],
            amount: totalExtra,
          });
          position.push([
            {
              type: "extra",
              size: [cWidth, row.extraSize],
            },
          ]);
          position.unshift([
            {
              type: "extra",
              size: [cWidth, row.extraSize],
            },
          ]);
          // }
        }
      } else if (row.amount == 0) {
        var amount = column.amount;
        var rowPosition = [];
        var splitProductInRow = splitOneProductByDimension(cHeight, pHeight);
        //console.log("splitProductInRow", splitProductInRow);
        let splitAmount = splitProductInRow.amount;
        let productAmount = Math.ceil(amount / splitAmount);
        //console.log("Need:", productAmount);

        totalProduct = +productAmount;
        for (let i = 1; i <= amount; i++) {
          rowPosition.push({
            type: "product",
            size: [pWidth, cHeight],
          });
        }

        let redundantAmount = splitAmount * productAmount - amount;

        if (column.needMod && column.modSize !== 0) {
          if (redundantAmount > 0) {
            rowPosition.push({
              type: "product",
              size: [column.modSize, cHeight],
            });
          } else {
            totalProduct += 1;
            let modWidth = column.modSize;
            let modHeight = cHeight;
            rowPosition.push({
              type: "product",
              size: [modWidth, modHeight],
            });
          }
        } else if (column.extraSize) {
          // Extra product
          //   if (redundantAmount > 0) {
          //     redundantAmount--;
          //     rowPosition.push(
          //       {
          //         type: "product",
          //         size: [column.extraSize, cHeight],
          //       },
          //     );
          //     rowPosition.unshift(
          //       {
          //         type: "product",
          //         size: [column.extraSize, cHeight],
          //       },
          //     );
          //   } else {
          extraProduct.push({
            size: [product[0], column.extraSize],
            amount: 1,
          });
          rowPosition.push({
            type: "extra",
            size: [column.extraSize, cHeight],
          });
          rowPosition.unshift({
            type: "extra",
            size: [column.extraSize, cHeight],
          });
          //   }
        }
        position.push(rowPosition);
      } else {
        var rowAmount = row.amount;
        var colAmount = column.amount;
        var redundantSizeByRow = [];
        var redundantExtraSizeByRow = [];

        for (let i = 1; i <= rowAmount; i++) {
          let rowPosition = [];
          let totalExtra = 0;
          for (let j = 1; j <= colAmount; j++) {
            totalProduct++;
            rowPosition.push({
              type: "product",
              size: [pWidth, pHeight],
            });
          }

          if (column.needMod && column.modSize !== 0) {
            if (redundantSizeByRow.length <= 0) {
              let splitProductInRow = splitOneProductByDimension(
                column.modSize,
                pWidth
              );
              //console.log("splitProductInRow", splitProductInRow);
              var splitAmount = splitProductInRow.amount;
              totalProduct++;
              for (let index = 0; index < splitAmount; index++) {
                redundantSizeByRow.push([column.modSize, pHeight]);
              }
            }

            rowPosition.push({
              type: "product",
              size: [column.modSize, pHeight],
            });
            redundantSizeByRow.pop();
          } else if (column.extraSize) {
            let exNumber = Math.floor(product[0] / pHeight);
            if (exNumber >= 2) {
              exNumber = 1;
              totalExtra += 1;
            } else {
              exNumber = 2;
              totalExtra += 2;
            }

            rowPosition.push({
              type: "extra",
              size: [column.extraSize, pHeight],
            });
            rowPosition.unshift({
              type: "extra",
              size: [column.extraSize, pHeight],
            });
          }

          if (totalExtra > 0) {
            extraProduct.push({
              size: [product[0], column.extraSize],
              amount: totalExtra,
            });
          }
          position.push(rowPosition);
        }

        // Last row
        var rowPosition = [];
        if (row.needMod && row.modSize !== 0) {
          let totalExtra = 0;
          var splitProductInColumn = splitOneProductByDimension(
            row.modSize,
            pHeight
          );
          //console.log("Last row splitProductInColumn: ", splitProductInColumn);
          var splitAmount = splitProductInColumn.amount;
          let productAmount = Math.ceil(column.amount / splitAmount);
          let modProduct = column.amount % splitAmount;
          var isRedundant = true;
          if (modProduct == 0 || modProduct == row.amount) {
            isRedundant = false;
          }
          totalProduct += productAmount;
          for (let i = 1; i <= column.amount; i++) {
            rowPosition.push({
              type: "product",
              size: [pWidth, row.modSize],
            });
          }

          if (column.needMod && column.modSize !== 0) {
            if (redundantExtraSizeByRow <= 0 && !isRedundant) {
              totalProduct += 1;
            }
            rowPosition.push({
              type: "product",
              size: [column.modSize, row.modSize],
            });
          } else {
            let exNumber = Math.floor(product[0] / row.modSize);
            if (exNumber >= 2) {
              exNumber = 1;
              totalExtra += 1;
            } else {
              exNumber = 2;
              totalExtra += 2;
            }

            extraProduct.push({
              size: [product[0], column.extraSize],
              amount: totalExtra,
            });

            rowPosition.push({
              type: "extra",
              size: [column.extraSize, row.modSize],
            });
            rowPosition.unshift({
              type: "extra",
              size: [column.extraSize, row.modSize],
            });
          }
          position.push(rowPosition);
        } else if (row.extraSize) {
          // Extra product in last row.
          let totalExtra = 0;
          let rowExtraSize = row.extraSize;
          let splitExtraNo = Math.floor(product[0] / pWidth);
          let needExtraNo = Math.ceil(column.amount / splitExtraNo);
          let modExtraSize = column.amount % splitExtraNo;

          totalExtra += needExtraNo;
          for (let i = 1; i <= column.amount; i++) {
            rowPosition.push({
              type: "extra",
              size: [pWidth, rowExtraSize],
            });
          }

          if (column.needMod && column.modSize !== 0) {
            if (modExtraSize < 1) {
              let val = Math.floor(product[0] / column.modSize);
              if (val >= 2) {
                totalExtra += 0.5;
              } else {
                totalExtra += 1;
              }
            }
            rowPosition.push({
              type: "extra",
              size: [column.modSize, rowExtraSize],
            });
          } else {
            let val = Math.floor(product[0] / column.extraSize);
            if (val >= 4) {
              totalExtra += 0.5;
            }

            rowPosition.push({
              type: "extra",
              size: [column.extraSize, rowExtraSize],
            });
            rowPosition.unshift({
              type: "extra",
              size: [column.extraSize, rowExtraSize],
            });
          }

          position.push(rowPosition);
          let val = JSON.parse(JSON.stringify(rowPosition));
          position.unshift(val);
          extraProduct.push({
            size: [product[0], row.extraSize],
            amount: totalExtra * 2,
          });
        }
      }
    }

    // Reserve position

    if (method == "h") {
      for (let i = 0; i < position.length; i++) {
        let row = position[i];
        // if (col.type == "extra") {
        //   break;
        // } else {
        if (i % 2) {
          row = row.reverse();
        }
        // }
      }
    } else {
      let isExtraRow = position[0][0].type == "extra" ? true : false;

      if (isExtraRow) {
        for (let i = 0; i < position.length; i++) {
          let row = position[i];
          if (row.type == "extra") {
            break;
          } else {
            if (i % 2) {
              row = row.reverse();
            }
          }
        }
      }

      let needReverse = true;
      for (let i = 0; i < position.length; i++) {
        let col = position[i];
        if (col.type == "extra") {
          needReverse = false;
          break;
        }
      }

      if (needReverse) {
        reverseColumn(position);
      }
    }

    let extraProductList = {};
    extraProduct.forEach((one) => {
      let amount = one.amount;
      let key = one.size[0] + "x" + one.size[1];
      let currentAmount = 0;
      if (extraProductList[key]) {
        currentAmount = extraProductList[key];
      }
      currentAmount += amount;
      extraProductList[key] = currentAmount;
    });

    return {
      total: totalProduct,
      extra: extraProductList,
      position,
      redundant,
    };
  }

  function reverseColumn(arr) {
    let h = arr.length;
    let w = arr[0].length;
    var newArr = JSON.parse(JSON.stringify(arr));
    for (let i = 0; i < w; i++) {
      if (i % 2) {
        for (let j = 0; j < h; j++) {
          arr[j][i] = newArr[h - (j + 1)][i];
          if (j > 0) {
            arr[j][i]["reverse"] =
              newArr[h - 1][i]["size"][1] - newArr[0][i]["size"][1];
          }
        }
      }
    }
    return arr;
  }

  return calculate();
}

export default calculateProduct;
