<template>
    <section>
        <b-container>
        <!-- Content here -->
            <b-row>
                <b-col cols="12">
                    <div class="d-flex justify-content-center">
                        <span class="font-weight-bold">CÔNG CỤ TÍNH SỐ LƯỢNG SẢN PHẨM</span>
                    </div>
                </b-col>
            </b-row>
            <b-row>
                <b-col sm="3">
                    <div class="d-flex flex-column justify-content-center">
                        <span>KÍCH THƯỚC SẢN PHẨM</span>
                        <b-form-select v-model="sizeSelected" :options="sizeList"></b-form-select>

                    </div>
                </b-col>
                <b-col sm="3">
                    <div class="d-flex flex-column justify-content-center">
                        <span>CHIỀU RỘNG</span>
                        <b-form-input v-model="width" placeholder="Chiều rộng khu vực ốp" type="number"></b-form-input>
                    </div>
                </b-col>
                <b-col sm="3">
                    <div class="d-flex flex-column justify-content-center">
                        <span>CHIỀU CAO</span>
                        <b-form-input v-model="height" placeholder="Chiều cao khu vực ốp" type="number"></b-form-input>
                    </div>
                </b-col>
                <b-col sm="3">
                    <div class="d-flex flex-column justify-content-center">
                        <span>CÁCH THỨC ỐP</span>
                        <b-form-select v-model="methodSelected" :options="methodList"></b-form-select>

                    </div>
                </b-col>
            </b-row>

            <b-row>
                <b-col cols="12">
                    <div class="d-flex justify-content-center">
                          <b-button variant="outline-primary" v-on:click="calculate">BẮT ĐẦU TÍNH</b-button>
                    </div>
                </b-col>
            </b-row>

            <b-row class="result" v-if="isDone">
                <b-col cols="12">
                    <div class="d-flex justify-content-start">
                        <ul>
                            <li>
                                Tổng số sản phẩm: {{totalResult}}
                            </li>
                            <li>
                            Tổng số nẹp:
                                <ul>
                                    <li v-for="(value, name) in extra" :key="value"> {{value}} nẹp ( {{name}})</li>
                                </ul>
                            </li>
                        </ul>
                    </div>
                </b-col>
            </b-row>
            <b-row class="result" v-if="isDone">
                <b-col cols="12">
                    <div class="d-flex justify-content-center">
                        <span class="font-weight-bold"> LAYOUT</span>
                    </div>
                    <div class="d-flex layout-row justify-content-center"
                         v-for="(row, index) in position" :key="index">
                        <div 
                         v-for="(column, i) in row"
                         :key="i"
                         v-bind:class="column.type =='extra' ? 'border-wood' : 'wood'"
                         data-toggle="tooltip"
                         v-bind:style="columnStyle(column)"
                          v-b-tooltip.hover 
                          :title="columnSizeTitle(column.size)"
                          >
                          <span class="product-size">
                              {{column.size[0]}}x{{column.size[1]}}
                          </span>
                        
                        </div>

                    </div>
                </b-col>
            </b-row>
        </b-container>
    </section>
</template>

<script>

import calculateProduct from "./product";

export default {
    name: "Product",
    components: {

    },
    data() {
        return {
            sizeList: [
                        { value: null, text: 'Chọn kích thước' },
                        { value: [240,120], text: 'Tấm ốp 2,4m x 1,2m' },
                        { value: [244,122], text: 'Tấm ốp 2,44m x 1,22m' },
                        { value: [305,120], text: 'Tấm ốp 3,05m x 1,2m' },
            ],
            // sizeSelected: null,
            sizeSelected: [240,120],
            methodList: [
                        { value: null, text: 'Chọn cách ốp' },
                        { value: 'h', text: 'Ốp ngang' },
                        { value: 'v', text: 'Ốp dọc' },
            ],
            // methodSelected: null,
            methodSelected: 'h',
            width: 10.5,
            height: 2.5,
            isDone: false,
            total: 0,
            extra: null,
            position: []
        };
    },
    computed: {
        totalResult() {
            return this.total + " tấm ( " + this.sizeSelected[0] + "x" + this.sizeSelected[1] + ")";
        },
       
    },
    methods: {
        calculate() {
            if (this.sizeSelected && this.methodSelected && this.width && this.height) {
                this.isDone = false;
                let width = Math.ceil(this.width * 100);
                let height = Math.ceil(this.height * 100);
                var result = calculateProduct([width, height], this.sizeSelected, this.methodSelected);
                this.total = result.total;
                this.extra = result.extra;
                this.position = result.position;
                this.isDone = true;
            } else {
                alert("Vui lòng đầy đủ các thông số!");
            }
        },

        columnStyle(column) {
            let width = column.size[0];
            let height = column.size[1];
            if (column.type == 'extra') {
                if (width <= 15) width = width * 6;
                if (height <= 15) height = height * 6;
            }
            let marginTop = '';
            if (column.reverse) {
                marginTop += 'margin-top:' + column.reverse + 'px;';
            }

            return 'width:' + width + 'px;height:' + height + 'px;' + marginTop;
        },

        columnSizeTitle(size) {
            return size[0] + 'x' + size[1];
        }
    }

}
</script>